export default {
  props: {
    uppercase: { type: Boolean, required: false, default: true },
    loading: { type: Boolean, required: false, default: false }
  },

  data () {
    return {
      search: ''
    }
  },

  watch: {
    search: {
      immediate: true,
      handler (newVal) {
        this.$emit('search', newVal)
      }
    }
  },

  methods: {
    fireEvent () {
      this.$emit('firedEvent', this.search)
    }
  }
}
